<template>
  <div>
    <vx-header :showback="1" :title="$t('settings.enterSource')"></vx-header>
    <!-- 源列表 -->
    <div class="vx-container">
      <div class="sel-sources">
        <vx-cell :class="{selected: selected.id === o.id}" :icon="o.status?'signal':'nosignal'" :title="o.name" :showLink="false" v-for="(o,i) in sources" :key="i" @link="selectSource(o)"></vx-cell>
      </div>
    </div>
  </div>
</template>
<script>
  import htp from "../../api/http";
  import urls from "../../api/apiurl";
  import { serialize, setLoc } from 'src/utils';
  export default {
    data() {
      return {
        selected: {},
        sources: []
      };
    },
    created() {
      this.selected = this.$route.params.selected;
      this.getInputSourceList();
    },
    methods: {
      getInputSourceList() {
        htp.get(urls.getInputSourceList).then(response => {
          if(response.status === 10000000) {
            this.sources = response.data.inputSourceList;
          }
        });
      },
      back() {
        this.$router.go(-1);
      },
      selectSource(o) {
        this.selected = o;
        setLoc('sel-source', this.selected);
      }
    }
  };
</script>
<style scoped lang="less">
  .sel-sources {
    padding-top: 25px;
    .vc-cell-title {
      color: #dddddd;
    }
    .vc-cell {
      border-bottom: 1px solid #4f5054; /*no*/
      &:last-child {
        border-bottom: none;
      }
    }
    .selected {
      background: ~"url('../../assets/common/checked.png') no-repeat" #383940;
      background-size: 32px 32px;
      background-position: calc(~'100% - 35px');
    }
  }
</style>


